.data-widget {
	display: flex;
	width: 100%;
	flex-direction: column;
	padding: 10px 20px;
	background-color: var(--fourth-color);
	border-radius: 20px;
	margin-bottom: 20px;
	box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.time-block {
	display: flex;
	justify-content: center;
	align-items: center;
}

.time-text {
	font-size: 52px;
	line-height: 73px;
	width: 195px;
	text-align: center;
}

.date-block {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}
.date-text {
	font-size: 22px;
	line-height: 27px;
}

@media (max-width: 568px) {
	.date-text {
		font-size: 16px;
		line-height: 20px;
	}
	.time-text {
		font-size: 40px;
		line-height: 50px;
		width: 140px;
	}
}
